html, body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
}

.theme-light {
  --color-primary: #0060df;
  --color-secondary: #fbfbfe;
  --color-accent: #fd6f53;
  --font-color: #000;
}

.theme-dark {
  --color-primary: #17ed90;
  --color-secondary: #2a2c2d;
  --color-accent: #12cdea;
  --font-color: #fff;
}

.container {
  width: 100%;
  height: 100%;
  background: var(--color-secondary);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container h1 {
  color: var(--font-color);
  font-family: sans-serif;
}

.container button {
  color: var(--font-color);
  background: var(--color-primary);
  border: 0;
  border-radius: 5px;
  padding: 10px 20px;
}

.switch {
  width: 60px;
  height: 34px;
  display: inline-block;
  position: relative;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 40px;
  width: 40px;
  background: #fff url("https://i.ibb.co/7JfqXxB/sunny.png") center no-repeat;
  margin: auto 0;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  box-shadow: 0 0 15px rgba(32, 32, 32, .24);
}

input:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  background: #fff url("https://i.ibb.co/FxzBYR9/night.png") center no-repeat;
  transform: translateX(24px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

p {
  position: absolute;
  top: 10px;
  left: 10px;
}

